import 'antd/dist/antd.css';
import '../App.css';
import axios from 'axios';
import { Link } from "react-router-dom";
import { Button, Input, Form, Radio, DatePicker,
  Select, Card, InputNumber, Checkbox, Upload, message } from 'antd';
import React, {  useState, useEffect } from 'react';
import TextArea from 'antd/lib/input/TextArea';
import reqwest from 'reqwest';


const { Option } = Select;



function Anketa(props) { 
  // Стейты для копирования файлов 
  const [fileDiplom, setFileDiplom ] = useState([])
  const [uploading, setUploading] = useState(false)
  // Функция для копирования файлов
  const handleUpload = () => {    
    const formDataDiplom = new FormData();   
    fileDiplom.forEach(file => {       
      formDataDiplom.append('file[]', file, file.name);           
    });   
    setUploading(true)  
    reqwest({
      url:'http://localhost:5001/upload',
      method: 'post',
      processData: false,
      data: formDataDiplom,     
      success: () => {        
        setUploading(false);
        setFileDiplom([]);       
        message.success({style: {marginTop: '20vh'},
        content:'Поздравляем! Ваша анкета успешно загружена',duration: 2});
      },
      error: () => {        
        setUploading(false);      
        message.error('upload failed.');
      },
    });
  };
  //предварительная обработка файла перед отправкой
  const beforeUpload = (file,b) => {    
    Object.defineProperty(file, 'name', {
      value: b,
      writable: true,
      enumerable: true,
      configurable: true
    });    
    if (file.type !== 'image/jpeg'  ) {
      message.error({content:`${file.name} не  jpg  файл !`,duration: 2});
    } else if( file.size > 2000000) {
      message.error({content:` Размер ${file.name} больше 2 мб !`,duration:2});
    } else {
      setFileDiplom([...fileDiplom, file]);
    }    
  } 
  // пропсы для отправки файла       
  const propDiplom = {    
    onRemove: file => {      
      const index = fileDiplom.indexOf(file);
      const newFileList = fileDiplom.slice();
      newFileList.splice(index, 1);       
      setFileDiplom(newFileList);       
    }
  }
  // расшифровка запроса на анкету
  const query = window.location.search.substring(1).split("=")[1]
  console.log( window.location.search)
  // определяем  выбранную анкету
  const anketaType = query === '1' ? 
    "Сбербанк Здоровье" : query === '2' ?  
    "врача из клиники партнера" : 
    "врача из регионального МО (для РГС)"
  
 
  const [anketa, setAnketa] = useState("")
  useEffect(()=>setAnketa(query),[query])  
  const [form] = Form.useForm()
  const [optWork, setOptWork] = useState([1])
  const [kurs, addKurs] = useState([1])
  // принимаем анкету с сервера 
  function GetAnkete(){
    axios.get(`https://jsonplaceholder.typicode.com/users`)
    .then(res =>
      { 
        console.log(Object.keys(res.data[0]));
        const s = Object.entries(res.data[0]);
        for( const ss of s ){         
          setFields([{name: [ss[0]], value: ss[1] }]);
          console.log(ss);
        };               
      }
    )
  }
  //отправляем анкету на сервер
  function ResiveAnkete(values) {    
    axios({
    method: 'post',    
    url: `https://jsonplaceholder.typicode.com/users`,
    data:{values}   
  })
  .then(res => {
    handleUpload();    
    console.log(res.data);
  })
    console.log("resive ankete")
  }
  const [fields, setFields] = useState()
  useEffect(()=> {
    if(query.length === 10){
    return GetAnkete()
    } 
  },[query])
  const optionsWithDisabled = [
    { label: 'Сбербанк здоровье', value: '1' },
    { label: 'врача из клиники партнера', value: '2' },
    { label: 'врача из регионального МО (для РГС)', disabled: true, value: '3'},
  ];
  //set type ankete
  const [typeAnkete, setTypeAnkete] = useState("0")
  const onChange4 = e => {
    console.log('radio4 checked', e.target.value);
    setTypeAnkete(e.target.value);
  };

  useEffect(()=>{
   if(query.length > 1) {setTypeAnkete("0")} else {setTypeAnkete(query)}
  },[query])

  return (
    <div className="App">     
      <Link to="/" className="App-header" title="на главную"/>
      {query.length > 5 && <>
        <h2>Выберите анкету для редактирования</h2>   
        <Radio.Group
            options={optionsWithDisabled}
            onChange={onChange4}
            size="large"
            value={typeAnkete}
            optionType="button"
            buttonStyle="solid"            
          />
        </>
      }
      { typeAnkete !=="0" && 
           <Form            
            layout="vertical"
            form={form}
            fields={fields}
            initialValues={{remember: true}}            
            onFinish={ResiveAnkete}
          >
          <h2>Анкета для трудоустройства в {anketaType}</h2>        
          <p style={{textAlign: 'left',color: 'red'}}>* обязательные для заполнения поля</p>
            {typeAnkete !== '1' ? 
              <Form.Item  name="nameMO" label="Название МО"                          
                rules={[{ required: true, message: 'Пожалуйста наберите название МО!',
                whitespace: true }]}              
              >
                <Input               
                  required
                  placeholder="название мо"
                  pattern="[А-Яа-я\s]{1,20}"
                  title="русскими буквами"                   
                />
              </Form.Item> :""
            }
            <Card title="ФИО" style={{marginBottom:10}}>
              <Form.Item name="family" label="Фамилия"                             
                rules={[{ required: true, message: 'Пожалуйста наберите фамилию!', 
                whitespace: false }]}        
              >
                <Input               
                  required
                  placeholder="фамилия"
                  pattern="[А-Я-а-я]{1,15}"                
                />
              </Form.Item>
              <Form.Item   label="Имя" name="name"             
                rules={[{ required: true, message: 'Пожалуйста введите имя!', 
                whitespace: false }]}            
              >
                <Input               
                  placeholder="Имя"
                  required              
                  title="русскими буквами"
                  pattern="[а-яА-Я]{1,15}"               
                />
              </Form.Item>
              <Form.Item  label="Отчество" name="twoName"
                rules={[{ required: true, message: 'Введите отчество'}]}                
              >
                <Input 
                  required              
                  placeholder="отчество"               
                  pattern="[А-Яа-я]{1,15}"               
                />
              </Form.Item>
            </Card>
            <Form.Item label="Email" name="email"
              rules={[{required: true,message: 'Введите валидный адрес'}]}
            >
              <Input 
                placeholder="Email"
                required                        
                pattern="[a-zA-Z]{3,}@[a-z]{2,}.[a-z]{2,3}"
              />
            </Form.Item>
            <Form.Item label="Телефон" name="phone"
              rules={[{ required: true, message: 'Пожалуйста введите ваш телефон в формате!'}]}
            
            >
              <Input 
                placeholder="XXXXXXXXXX" 
                title="10 цифр без пробелов и тире"
                addonBefore="+7"               
                pattern="[0-9]{3}-[0-9]{3}-[0-9]{2}-[0-9]{2}"          
                
              />
            </Form.Item>
            <Form.Item label="Снилс" name="snils"
              rules={[{ required: true, message: 'Пожалуйста введите ваш СНИЛС!'}]} 
            >
              <Input.Password 
                placeholder="ХХХХХХХХХХХ"
                title="11 цифр без пробелов и тире"                
                pattern="[0-9]{3}-[0-9]{3}-[0-9]{3} [0-9]{2}"               
              />
            </Form.Item>
            {anketa !== '3' ? <Form.Item label="Дата рождения" name="dateOfBirth"
              rules={[{ required: true, message: 'Пожалуйста выберите дату рождения!'}]} 
            >
              <DatePicker  placeholder="дата рождения"  style={{width: 200,float:'right',marginTop:"-50px"}}/>
             
            </Form.Item>: ""}
            <Form.Item label="Город проживания" name="cityResidence"
              rules={[{ required: true, message: 'Пожалуйста выберите город проживания!'}]} 
            >
              <Input 
                placeholder="Город проживания"                
                pattern="[а-яА-Я]{1,15}"              
                
              />
            </Form.Item>
            <Form.Item label="Основная специальность" name="mainSpecialy"
              rules={[{ required: true, message: 'Пожалуйста введите основную специальность!'}]} 
            >
              <Input 
                placeholder="основная специальность"                
                pattern="[а-яА-Я\s]{1,}" 
                required              
              />
            </Form.Item>
            <Form.Item label="Дополнительная специальность" name="AddSpecialty">
              <Input 
                placeholder="дополнительная специальность"               
                pattern="[а-яА-Я]{1,15}"               
              />
            </Form.Item>
            <Form.Item label="Специализация" name="Specialization">
              <Input 
                placeholder="специализация"                
                pattern="[а-яА-Я]{1,15}"              
                
              />
            </Form.Item>
            {typeAnkete !=="3" ?
            <Card>
                <Form.Item label="Какой ведете прием" name="patient"
                  rules={[{ required: true, message: 'Пожалуйста выберите тип приема!' }]} 
                >
                  <Select style={{textAlign:'left'}} placeholder="вид приема">
                    <Option value="Детский">ДЕТСКИЙ</Option>
                    <Option value="Взрослый">ВЗРОСЛЫЙ</Option>
                  </Select>
                </Form.Item>
                <Form.Item  
                  shouldUpdate={(prevValues, currentValues) => prevValues.patient !== currentValues.patient}
                > 
                  {({ getFieldValue }) =>
                  getFieldValue('patient') === 'Детский' ? (               
                  <Form.Item label="C какого возвраста" name="agePatient"> 
                    <InputNumber style={{width: 100,float:'right',marginTop:"-50px"}}                                  
                      min={1}
                      max={20}                       
                    />
                  </Form.Item>) : null}
                </Form.Item>              
            </Card> : "" }           
            <Form.Item label="Год начала врачебной практики" style={{margin:0}} name="yearBeginPractic"> 
              <InputNumber style={{width: 100,float:'right',margin:"-50px 0 0"}}
                initialValue={1970}              
                min={1970}
                max={2020}               
              />           
            </Form.Item>                           
              <Upload  className="addPhoto" {...propDiplom} status="done"
                beforeUpload={(e)=>beforeUpload(e,'diplom.jpg')} 
              >
                <Button>* Добавить фото диплома</Button>
              </Upload>                     
                  <Button
                    type="primary"
                    onClick={handleUpload}
                    disabled={fileDiplom.length === 0}
                    loading={uploading}
                    style={{ marginTop: 16 }}
                  >
                    {uploading ? 'Загрузка' : 'Загрузить'}
                  </Button>
           
                
            {typeAnkete === "3" ? <Form.Item label="Расписание работы" name="sedule">
                <Input 
                  placeholder="расписание работы"                       
                  pattern="[а_Я0-9-,]{1,}"           
                        
                />
              </Form.Item>: ""
            }
            {typeAnkete !=="3" ? <>            
            <Card title="Базовое образование">               
              <Form.Item label="Год окончания" name="EndBaseEducation" style={{height:25}}> 
                <InputNumber style={{width: 100,float:'right'}}
                  initialValue={1970}              
                  min={1970}
                  max={2020}                   
                />
              </Form.Item>
              <Form.Item label="Название учреждения" name="NameSchoolBaseEducation">
                <Input 
                  placeholder="Название учреждения"                 
                  pattern="[а-яА-Я]{1,15}"  
                        
             />
              </Form.Item>
                <Form.Item label="Специализация" name="BaseSpecialization">
                    <Input 
                      placeholder="Специализация"                      
                      pattern="[а-яА-Я]{1,15}"               
                        
                    />
                </Form.Item>
                <Form.Item label="Город" name="CityBaseEducation">
                    <Input 
                      placeholder="Город"                     
                      pattern="[а-яА-Я]{1,15}"          
                       
                    />
              </Form.Item>           
            </Card>
            <Card title="Интернатура/Ординатура">              
                <Form.Item label="Год окончания" name="DateEndInternat" style={{height:25}}> 
                  <InputNumber style={{width: 100,float:'right'}}
                    initialValue={1970}              
                    min={1970}
                    max={2020}               
                      
                  />
                </Form.Item>
                <Form.Item label="Название учреждения" name="NameSchoolInternat">
                    <Input 
                      placeholder="Название учреждения"                       
                      pattern="[а-яА-Я]{1,15}"                   
                       
                    />
                </Form.Item>
                <Form.Item label="Специализация" name="SpecializationInternat">
                    <Input 
                      placeholder="Специализация"                       
                      pattern="[а-яА-Я]{1,15}"                   
                        
                    />
                </Form.Item>
                <Form.Item label="Город" name="CityEducationInternat">
                    <Input 
                      placeholder="Город"                        
                      pattern="[а-яА-Я]{1,15}"                   
                        
                    />
                </Form.Item>
           
            </Card>
            <Card title="Аспирантура/Докторантура">               
              <Form.Item label="Год окончания" name="DateEndAspirant" style={{height:25}}> 
                <InputNumber style={{width: 100,float:'right'}}                                 
                  min={1970}
                  max={2020}                  
                />
              </Form.Item>
              <Form.Item label="Название учреждения" name="NameSchoolAspirant">
                <Input 
                  placeholder="Название учреждения"                 
                  pattern="[а-яА-Я]{1,15}"           
                        
                />
              </Form.Item>
              <Form.Item label="Специализация" name="SpecializationAspirant">
                    <Input 
                      placeholder="Специализация"                       
                      pattern="[а-яА-Я]{1,}"                   
                        
                    />
              </Form.Item>
              <Form.Item label="Город" name="CityEducationAspirant">
                    <Input 
                      placeholder="Город"                     
                      pattern="[а-яА-Я]{1,15}"                
                        
                    />
              </Form.Item>           
            </Card>
            <Card title="Действующие сертификаты">            
              <div>
                <Form.Item label="Сертификат" name="SertificatOne"
                  rules={[{ required: true, message: 'Добавить сертификат!', whitespace: true }]}
                >
                      <Input 
                        placeholder="Название сертификата"                                                  
                        pattern="[а-яА-Я ,-\s]{1,}"                           
                      />
                </Form.Item>
                <Form.Item  label="Год окончания" name="DateEndSertificate" style={{height:25}}
                  rules={[{ required: true }]}
                > 
                  <InputNumber style={{width: 100,float:'right'}}
                    initialValue={2021}              
                    min={2021}
                    max={2030}                        
                  />
                </Form.Item>                     
                                 
                  <Upload className="addPhoto" {...propDiplom} 
                    beforeUpload={(e)=>beforeUpload(e,"sertificat.jpg")} 
                  >
                    <Button>* Добавить фото сертификата</Button>
                  </Upload>                     
                  <Button
                    type="primary"
                    onClick={handleUpload}
                    disabled={fileDiplom.length === 0}
                    loading={uploading}
                    style={{ marginTop: 16 }}
                  >
                    {uploading ? 'Загрузка' : 'Загрузить'}
                  </Button>
                          
              </div>
            </Card>
            <Form.Item label="Врачебная категория" name="Category">
              <Input 
                placeholder="Врачебная категория"               
                pattern="[а-яА-Я]{1,15}"               
              />
            </Form.Item>
            <Form.Item label="Ученая степень" name="AcademicDegre">
              <Input 
                placeholder="Ученая степень"                
                pattern="[а-яА-Я]{1,15}"           
                
              />
            </Form.Item>
            <Card title="Курсы повышения квалификации,семинары, мастерклассы">          
              {kurs.map(i => <React.Fragment key={i+"aa"}>
                <div>                   
                    <Form.Item label="Год окончания" name={`DateEndCurse${i}`}  
                      style={{height: '50px', margin:'10px 0'}}
                    > 
                      <InputNumber style={{width: 100,float:'right'}}                                       
                        min={2000}
                        max={2021}                          
                      />
                    </Form.Item> 
                    <Form.Item label="Наименование курса" name={`NameCurse${i}`}>
                      <Input 
                        placeholder="наименование курса"                    
                        pattern="[а-яА-Я\s]{1,}"                   
                            
                      />
                    </Form.Item>
                    <Form.Item label="Наименование учреждения" name={`NameCurseDepartment${i}`}>
                      <Input 
                        placeholder="наименование учреждения"                   
                        pattern="[а-яА-Я\s]{1,}"        
                       
                      />
                    </Form.Item>
                    <Form.Item label="Специальность"  name={`NameCurseSpec${i}`}>
                      <Input 
                        placeholder="специальность"                           
                        pattern="[а-яА-Я\s]{1,}"       
                       
                      />
                    </Form.Item>                    
                    <div className="addPhoto">                     
                      <Upload {...propDiplom} beforeUpload={(e)=>beforeUpload(e,`kurs${i}.jpg`)} >
                        <Button>Добавить фото курсов</Button>
                      </Upload>                      
                    </div>               
                    <hr/>
                </div>
              </React.Fragment>)}
              <p className="add" onClick={()=>addKurs([...kurs,kurs[kurs.length-1] +1])}>добавить курсы</p>
            </Card>
            <Form.Item label="Членство в професиональных организациях" name="profcom">
              <Input 
                placeholder="Да/Нет"               
                pattern="[а-яА-Я]{1,15}"               
              />
            </Form.Item>
            <Card title="Опыт работы">            
               
                  {optWork.map(i => <React.Fragment key={i}>
                    <Form.Item label="Год начала" name={`DateBeginWork${i}`}
                      style={{height: '50px', margin:'10px 0'}}
                    > 
                      <InputNumber style={{width: 100,float:'right'}}                                    
                        min={1980}
                        max={2021}                        
                      />
                    </Form.Item> 
                    <Form.Item label="Год окончания" name={`DateEndWork${i}`} 
                      style={{height: '50px', margin:0}}
                    > 
                      <InputNumber style={{width: 100,float:'right'}}                                
                        min={1970}
                        max={2021}                        
                      />
                    </Form.Item> 
                    <Form.Item label="Специальность" name={`nameSpecWork${i}`}>
                      <Input 
                        placeholder="специальность"                                               
                        pattern="[а-яА-Я\s]{1,}"                           
                      />
                    </Form.Item>
                    <Form.Item 
                      label="Наименование организации" 
                      name={`NameWorkDepartment${i}`}                      
                    >
                      <Input 
                        placeholder="наименование организации"                                                     
                        pattern="[а-яА-Я\s]{1,}"                          
                      />
                    </Form.Item>
                    <hr/>
                  </React.Fragment>)}                    
                  <p className="add" 
                    onClick={()=>setOptWork([...optWork,optWork[optWork.length-1]+1])}
                  >добавить место работы</p>

               
            </Card>
            <Form.Item label="Награды" name="rewards">
              <Input 
                placeholder="награды"                
                pattern="[а-яА-Я\s]{1,}"                   
                
              />
            </Form.Item>
            <Form.Item label="Фиксированые достижения" name="achievements">
              <Input 
                placeholder="фиксированые достижения"              
                pattern="[а-яА-Я\s]{1,}"                
              />
            </Form.Item>
            <Form.Item label="Научная деятельность" name="scientificActivity">
              <Input 
                placeholder="научная деятельность"                
                pattern="[а-яА-Я\s]{1,}"                
              />
            </Form.Item>
            </>:""}
            <Form.Item label="Описание" name="subscription">
              <TextArea rows={5}
                placeholder="научная деятельность"               
                pattern="[а-я,.А-Я\s]{1,}"
                
              />
            </Form.Item>
           
            {query.length>5 && <>
            <h3>Дозаполнение данных для 2-го этапа</h3>
            <div className="addAnketa">                     
                  <Upload className="addAnketa" {...propDiplom} 
                    beforeUpload={(e)=>beforeUpload(e,"medbook.jpg")}
                  >
                    <Button>* Фото всех страниц медкнижки</Button>
                  </Upload>                             
                  <Upload className="addAnketa"{...propDiplom} 
                    beforeUpload={(e)=>beforeUpload(e,"militaryID.jpg")}
                  >
                    <Button>* Фото военного билета</Button>
                  </Upload>                                
                  <Upload className="addAnketa" {...propDiplom} 
                    beforeUpload={(e)=>beforeUpload(e,"propatolog.jpg")}
                  >
                    <Button>* Заключение пропатолога</Button>
                  </Upload>                 
                  <Upload className="addAnketa" {...propDiplom} 
                    beforeUpload={(e)=>beforeUpload(e,"alco.jpg")}
                  >
                    <Button>* Заключение нарколога</Button>
                  </Upload>                 
                  <Upload className="addAnketa" {...propDiplom} 
                    beforeUpload={(e)=>beforeUpload(e,"psiho.jpg")}
                  >
                    <Button>* Заключение психиатра</Button>
                  </Upload>                 
                  <Upload className="addAnketa" {...propDiplom} 
                    beforeUpload={(e)=>beforeUpload(e,"myFoto.jpg")}
                  >
                    <Button>* Ваше фото</Button>
                   
                    <span className="addAnketaText" >
                     
                      <b>Фото</b> в анфас, белый медицинский халат, 
                      лицо хорошо видно , голова/плечи не срезаны - силуэт хорошо виден
                      полностью, размер минимум 400*400 пикселей, не более 2мб,
                      цветная фотография
                    </span>
                  </Upload>                 
              </div>                              
            </>}
           
            <Form.Item
              name="agreement"
              valuePropName="checked"
              rules={[
                {
                  validator: (_, value) =>
                    value ? Promise.resolve() : Promise.reject(new Error('Вы должны принять соглашение')),
                },
              ]}
       
            >
        <Checkbox>Согласен на обработку персональных данных 
         
        </Checkbox>
      </Form.Item>
          <Button type="primary" block htmlType="submit">Отправить</Button>
          </Form> }       
      </div>
    );
}

export default Anketa;